.postImageCont {
  align-items: center;
  display: flex; }
  .postImageCont .width100 {
    width: 100%; }
  .postImageCont a {
    width: 100%; }
    .postImageCont a .postImageFigure {
      width: 100%;
      height: 200px;
      overflow: hidden; }
      .postImageCont a .postImageFigure .postImage {
        object-fit: cover;
        height: 100%;
        width: 100%; }

.postTextBlock {
  padding: 15px 0;
  order: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .postTextBlock .postTime {
    font-size: 14px;
    color: #aaa; }
  .postTextBlock .postDesc {
    font-size: 16px;
    color: #767676;
    line-height: 1.4;
    font-family: "IBM Plex Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Helvetica Neue", Arial, sans-serif;
    margin-bottom: 10px;
    margin-top: 0px; }
  .postTextBlock .postTitle {
    font-size: 21px;
    font-weight: 700;
    padding-top: 4px;
    padding-bottom: 8px;
    line-height: 1.2;
    margin: 0px;
    font-family: "Gelasio", serif; }
    .postTextBlock .postTitle .postLink {
      color: #000; }
  .postTextBlock .postHeader {
    display: flex;
    align-items: center; }
    .postTextBlock .postHeader .postTags {
      margin: 0px;
      list-style: none;
      padding: 0px;
      display: flex; }
      .postTextBlock .postHeader .postTags .postTag {
        font-size: 12px;
        display: inline-block; }
        .postTextBlock .postHeader .postTags .postTag a {
          color: #6A0BFF;
          text-transform: uppercase;
          letter-spacing: 0.5px;
          margin-right: 10px; }
  .postTextBlock .postFooter {
    display: flex;
    align-items: center; }

.post {
  margin-bottom: 50px;
  display: flex;
  width: calc(33.33333% - 50px);
  flex-direction: column;
  padding-left: 25px;
  padding-right: 25px; }
  .post:hover .postImage {
    transform: scale(1.05);
    transition: transform 3s cubic-bezier(0.35, 0.9, 0.5, 1); }
  .post:first-child {
    flex-direction: row;
    width: 100%; }
    .post:first-child .postTags .postTag a {
      color: #e3f96a; }
    .post:first-child .postImageCont {
      flex: 3; }
    .post:first-child .postTextBlock {
      flex: 2;
      padding: 40px;
      background: #6A0BFF; }
      .post:first-child .postTextBlock .postTime {
        color: rgba(255, 255, 255, 0.6); }
    .post:first-child .postTitle {
      font-size: 36px;
      line-height: 1.1; }
      .post:first-child .postTitle a {
        color: #fff; }
    .post:first-child .postDesc {
      color: #fff;
      font-size: 18px; }
    .post:first-child .postImageFigure {
      height: 400px; }

.postList {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap; }
