.header {
  padding: 15px 15px;
  background: #fff;
  border-bottom: 1px solid #ddd; }

.wrapper {
  display: flex;
  max-width: 1160px;
  margin: 0 auto; }
  .wrapper .brand .logo {
    margin-left: 25px;
    display: flex; }
    .wrapper .brand .logo p {
      margin: 0;
      line-height: 36px;
      margin-left: 10px;
      color: #aaa; }
    .wrapper .brand .logo a {
      height: 36px;
      display: flex;
      align-items: center; }
      .wrapper .brand .logo a svg {
        height: 24px;
        width: 180px; }
        .wrapper .brand .logo a svg path, .wrapper .brand .logo a svg polygon, .wrapper .brand .logo a svg circle {
          fill: #000; }

.titleLink {
  color: #000;
  text-decoration: none; }

.hamCont .ham {
  position: absolute;
  top: 25px;
  left: 30px;
  cursor: pointer; }
  .hamCont .ham span {
    width: 20px;
    height: 1px;
    display: block;
    background-color: #000; }
    .hamCont .ham span:nth-child(2) {
      margin: 6px 0; }

.menuCont {
  flex: 1;
  text-align: right; }

.socialCont ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  height: 36px;
  align-items: center;
  margin-right: 25px; }
  .socialCont ul li {
    margin-left: 10px; }
    .socialCont ul li a {
      height: 36px;
      color: #000;
      display: flex;
      align-items: center; }
      .socialCont ul li a svg {
        width: 20px;
        height: 20px; }
        .socialCont ul li a svg path, .socialCont ul li a svg circle {
          fill: #000; }

.menu {
  list-style: none;
  max-width: 1160px;
  margin: 0 auto;
  margin-bottom: 0px; }
  .menu li {
    display: inline;
    margin-right: 25px;
    font-size: 14px; }
    .menu li a {
      color: #000;
      text-decoration: none;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-size: 13px;
      line-height: 36px; }
      .menu li a:hover {
        color: #767676; }

main {
  background-color: #fafafa; }
  main .content {
    padding: 30px 0px;
    background: #fff;
    max-width: 1160px;
    margin: 0 auto; }

footer {
  border-top: 1px solid #ddd;
  padding: 40px 20px; }
  footer .footerMax {
    max-width: 1160px;
    margin: 0 auto; }
    footer .footerMax .footerTop {
      display: flex;
      color: #767676;
      padding: 10px 25px; }
      footer .footerMax .footerTop .copyright {
        flex: 1; }
      footer .footerMax .footerTop .footerLinks {
        flex: 1;
        justify-content: flex-end;
        text-align: right;
        display: flex;
        list-style: none;
        font-size: 14px;
        margin: 0; }
        footer .footerMax .footerTop .footerLinks li a {
          color: #767676;
          margin-left: 25px; }
    footer .footerMax .footerBottom {
      color: #aaa;
      font-size: 12px;
      padding: 1px 25px; }
